*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 10px;
    color: #c9d1d9;
}

body{
    background-color: #0d1117;
}

textarea{
    width: 25rem;
    min-width: 25rem;
    max-width: 25rem;
    background: none;
    border: none;
    color: white;
    resize: none;
}

textarea:focus{
    outline: none;
}

.body{
    position: absolute;
    height: 100%;
    width: 100%;

    display: grid;
    grid-template-rows: 10rem 1fr;
    justify-items: center;
}

.top-bar{
    grid-row: 1/2;
    display: grid;
    height: 100%;
    width: 90%;
    align-items: center;
}



.new-list-button{

    justify-self: start;

    display: grid;
    background-color: #2EA043;
    border: none;
    border-radius: 1rem;
    height: 4rem;
    width: 14rem;
    grid-template-columns: 5rem 1fr;

    align-items: center;
    justify-content: center;
}

.new-list-button #text{
    justify-self: start;
    font-size: 1.8rem;
    color: white;
}


.main-section{
    width: 95%;
    display: grid;

    justify-content: center;
    justify-items: center;
}

.list{
    margin-bottom: 5rem;
    width: 40rem;
    border: rgba(255,255,255,0.4) solid 1px;
    border-radius: 1.5rem;
    background-color: #161B22;

    display: grid;
    grid-template-rows: auto auto;
    justify-content: center;
}

.list-top-bar {
    width: 100%;
    margin-top: 3rem;
    display: grid;
    grid-template-columns: 25rem 5rem;
}

.list-top-bar ::-webkit-scrollbar{
    width: 0;
}

.heading{
    font-size: 2.2rem;
    font-weight: bold;
    height: 2.2rem;
}



.delete-button{
    height: 3rem;
    width: 3rem;
    display: grid;
    align-self: start;
    align-items: center;
    justify-items: center;
    border-radius: 1rem;
    justify-self: end;
    border: none;
    background-color: #FB5B63;
}

.delete-button path{
    fill: white;
}

.list-item-holder{
    width: 30rem;
    margin-top: 3rem;
    margin-bottom: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.list-item-holder .item{
    width: 100%;
    display: grid;
    margin-bottom: 2rem;
    grid-template-columns: 3rem 25rem;
    align-items: center;
    justify-content: center;
    justify-items: center;
}

.list-item-holder .item ::-webkit-scrollbar{
    width: 0;
}

.check-button{
    justify-self: start;
    align-self: start;
    margin-top: 0.5rem;

    height: 1.5rem;
    width: 1.5rem;
    border: rgba(255,255,255,0.5) solid 1px;
    border-radius: 0.75rem;
    background: none;
    display: grid;
    align-items: center;
    justify-items: center;
    justify-content: center;
}

.check-circle{
    justify-self: center;
    height: 0.75rem;
    width: 0.75rem;
    border-radius: 0.375rem;
    background-color: white;
}

.content{
    font-size: 1.8rem;
    min-height: 2rem;
}

.strike-content{
    font-size: 1.8rem;
    min-height: 2rem;
    text-decoration: line-through;
}